import { __ } from '@wordpress/i18n';
import { InspectorControls} from '@wordpress/block-editor';
import { PanelBody, SelectControl, Modal } from '@wordpress/components';
import { useState } from '@wordpress/element';
import { useBlockProps } from '@wordpress/block-editor';



const Edit = ({ attributes, setAttributes }) => {
    const { selectedIcon } = attributes;
    const [isModalOpen, setIsModalOpen] = useState(false);
    

    const iconOptions = [
        { value: 'fa-heart', label: 'Heart' },
        { value: 'fa-star', label: 'Star' },
        { value: 'fa-smile', label: 'Smile' },
        { value: 'fa-thumbs-up', label: 'Thumbs Up' },
        { value: 'fa-check', label: 'Check' },
        { value: 'fa-envelope', label: 'Envelope' },
        { value: 'fa-gears', label: 'Gears'},
        { value: 'fa-network-wired', label: 'Network Wired'},
        { value: 'fa-facebook-square', label: 'Facebook'}
    ];

    const toggleModal = () => {
        setIsModalOpen(!isModalOpen);
    };

    const handleSelectIcon = (value) => {
        setAttributes({ selectedIcon: value });
        toggleModal();
    };

   

    return (
        <>
            <InspectorControls>
                <PanelBody title={__('Icon Settings', 'text-domain')}>
                    <SelectControl
                        label={__('Select Icon', 'text-domain')}
                        value={selectedIcon}
                        options={iconOptions.map(option => ({
                            value: option.value,
                            label: option.label
                        }))}
                        onChange={(value) => setAttributes({ selectedIcon: value })}
                        onFocus={toggleModal}
                    />
                </PanelBody>
            </InspectorControls>
            <div {...useBlockProps()}>
                </div>
                {isModalOpen && (
                    <Modal
                        title={__('Select Icon', 'text-domain')}
                        onRequestClose={toggleModal}
                    >
                        <div>
                            {iconOptions.map((option) => (
                                <button
                                    key={option.value}
                                    onClick={() => handleSelectIcon(option.value)}
                                    >
                                    <i className={`fa ${option.value}`}></i> 
                                </button>
                            ))}
                        </div>
                    </Modal>
                )}
               
             <i className={`fa ${selectedIcon}`}></i>
        
            
        </>
    );
};

export default Edit;
